import 'velocity-animate'
import 'velocity-animate/velocity.ui.min'
import selectComponent from 'utilities/select-component'
import { $bpLg } from 'utilities/grid-settings'
import 'vendor/jquery.debouncedresize'

export default class Nav {
    bodyOpenClass = '-nav-open'
    mobileToggleActiveClass = 'is-active'

    constructor(element, options = {}) {
        this.$component = selectComponent(element)
        this.$nav = selectComponent(this.$component.attr('data-target'))
        this.attachEvents()
    }

    attachEvents() {
        this.checkToggle()

        $(window).on('debouncedresize', () => {
            this.checkToggle()
        })
    }

    checkToggle() {
        this.stopToggle()

        if(!$bpLg.matches) {
            this.startToggle()
        }
    }

    startToggle() {
        this.$component.on('click', () => {
            if (this.$component.attr('data-property') == 'open') {
                this.$component.attr('data-property', 'closed')
                this.closeNav()
                this.stopClickOffEvent()
                this.$component.removeClass(this.mobileToggleActiveClass)
                $('body').removeClass(this.bodyOpenClass)
            } else {
                this.$component.attr('data-property', 'open')
                this.openNav()
                this.startClickOffEvent()
                this.$component.addClass(this.mobileToggleActiveClass)
                $('body').addClass(this.bodyOpenClass)
            }
        })
    }

    stopToggle() {
        this.$component.off()
    }

    startClickOffEvent = () => {
        $(document).on('click', () => {
            this.closeNav()
        })

        this.$nav.on('click', (event) => {
            event.stopPropagation()
        })

        this.$component.on('click', (event) => {
            event.stopPropagation()
        })
    }

    stopClickOffEvent = () => {
        $(document).off('click')
    }

    openNav() {
        this.$nav.elements.navList.velocity('slideDown', {
            duration: 400,
            complete: () => {
                this.$nav.elements.navList.removeAttr('style')
                this.$nav.elements.navList.attr('data-property', 'open')
            }
        })

        this.$nav.elements.navItem.velocity('transition.slideLeftIn', {
            stagger: 75,
            display: null,
            drag: false,
            duration: 500,
            complete: () => {
                this.$nav.elements.navItem.removeAttr('style')
            }
        })
    }

    closeNav() {
        this.$nav.elements.navList.velocity('slideUp', {
            duration: 500,
            complete: () => {
                this.$nav.elements.navList.removeAttr('style')
                this.$nav.elements.navList.attr('data-property', 'closed')
            }
        })

        this.$nav.elements.navItem.velocity('fadeOut', {
            stagger: 25,
            display: null,
            drag: false,
            duration: 250,
            backwards: true,
            complete: () => {
                setTimeout(() => {
                    this.$nav.elements.navItem.removeAttr('style')
                }, 250)
            }
        })
    }
}


