import AjaxForm from 'component/ajax-form'
import Carousel from 'component/carousel'
import CustomInput from 'component/custom-input'
import modalMFP from 'component/modal-mfp'
import Nav from 'component/nav'
import SubNav from 'component/subnav'
import PhotoSwipe from 'component/gallery-photoswipe'
import selectComponent from 'utilities/select-component'
import Share from 'component/share'

const AUTO_MODULES = [
    PhotoSwipe
]

export default function Site() {

    const init = () => {
        startDefault()
        startCustom()
    }

    const startDefault = function() {
        $.each(AUTO_MODULES, (key, Module) => {
            const instance = Module()
            instance.start()
        })
    }

    const startCustom = () => {
        selectComponent('select').each((index, element) => {
            new CustomInput(element)
        })

        selectComponent('ajax-form').each((index, element) => {
            new AjaxForm(element)
        })

        selectComponent('toggle-nav').each((index, element) => {
            new Nav(element)
        })

        selectComponent('sub-nav').each((index, element) => {
            new SubNav(element)
        })

        selectComponent('share').each((index, element) => {
            new Share(element)
        })

        const homepageSlider = Carousel('.carousel')
        homepageSlider.start({
            autoplaySpeed: 5000,
        })
    }

    return {
        init
    }
}


